<template>
  <view class="container">
    <!-- Ê¨¢ËøéÂå∫Âüü -->
    <view class="welcome-section">
      <view class="welcome-title">ÊàøÈó¥ÁÆ°ÁêÜÁ≥ªÁªü</view>
      <view class="welcome-subtitle">È´òÊïàÁÆ°ÁêÜÊÇ®ÁöÑÊàø‰∫ßËµÑÊ∫ê</view>
    </view>

    <!-- ÁªüËÆ°Âç°Áâá -->
    <view class="stats-grid">
      <view class="stat-card" @click="navigateToRooms('')">
        <view class="stat-icon total">üè†</view>
        <view class="stat-number">{{ statistics.total }}</view>
        <view class="stat-label">ÊÄªÊàøÈó¥Êï∞</view>
      </view>
      
      <view class="stat-card" @click="navigateToRooms('rented')">
        <view class="stat-icon rented">üîë</view>
        <view class="stat-number">{{ statistics.rented }}</view>
        <view class="stat-label">Â∑≤Âá∫Áßü</view>
      </view>
      
      <view class="stat-card" @click="navigateToRooms('available')">
        <view class="stat-icon available">üÜì</view>
        <view class="stat-number">{{ statistics.available }}</view>
        <view class="stat-label">Á©∫Èó≤ÊàøÈó¥</view>
      </view>
      
      <view class="stat-card">
        <view class="stat-icon revenue">üí∞</view>
        <view class="stat-number">{{ statistics.monthlyRevenue }}</view>
        <view class="stat-label">ÊúàÊî∂ÂÖ•(ÂÖÉ)</view>
      </view>
    </view>

    <!-- Âø´Êç∑Êìç‰Ωú -->
    <view class="quick-actions">
      <view class="section-title">Âø´Êç∑Êìç‰Ωú</view>
      <view class="action-grid">
        <view class="action-item" @click="addRoom">
          <view class="action-icon">‚ûï</view>
          <text class="action-text">Ê∑ªÂä†ÊàøÈó¥</text>
        </view>
        
        <view class="action-item" @click="viewRooms">
          <view class="action-icon">üìã</view>
          <text class="action-text">ÊàøÈó¥ÂàóË°®</text>
        </view>
        
        <view class="action-item" @click="utilityRecords">
          <view class="action-icon">‚ö°</view>
          <text class="action-text">Ê∞¥ÁîµÁÆ°ÁêÜ</text>
        </view>
        
        <view class="action-item" @click="tenantManagement">
          <view class="action-icon">üë•</view>
          <text class="action-text">ÁßüÊà∑ÁÆ°ÁêÜ</text>
        </view>
        
        <view class="action-item debug-item" @click="systemTest">
          <view class="action-icon">üîß</view>
          <text class="action-text">Á≥ªÁªüÊµãËØï</text>
        </view>
      </view>
    </view>

    <!-- ÊúÄËøëÊ¥ªÂä® -->
    <view class="recent-activity">
      <view class="section-title">ÊúÄËøëÊ¥ªÂä®</view>
      <view class="activity-list">
        <view class="activity-item" v-for="activity in recentActivities" :key="activity.id">
          <view class="activity-icon">{{ activity.icon }}</view>
          <view class="activity-content">
            <view class="activity-title">{{ activity.title }}</view>
            <view class="activity-time">{{ formatTime(activity.time) }}</view>
          </view>
        </view>
        
        <view class="no-activity" v-if="recentActivities.length === 0">
          <text>ÊöÇÊó†ÊúÄËøëÊ¥ªÂä®</text>
        </view>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      statistics: {
        total: 0,
        rented: 0,
        available: 0,
        monthlyRevenue: 0
      },
      recentActivities: []
    }
  },
  
  onLoad() {
    this.loadStatistics();
    this.loadRecentActivities();
  },
  
  onShow() {
    // È°µÈù¢ÊòæÁ§∫Êó∂Âà∑Êñ∞Êï∞ÊçÆ
    this.loadStatistics();
  },
  
  methods: {
    // Âä†ËΩΩÁªüËÆ°Êï∞ÊçÆ
    async loadStatistics() {
      try {
        // Ëé∑ÂèñÊàøÈó¥ÁªüËÆ°
        const promises = [
          this.getRoomCount(''),
          this.getRoomCount('rented'),
          this.getRoomCount('available')
        ];
        
        const results = await Promise.all(promises);
        
        this.statistics.total = results[0];
        this.statistics.rented = results[1];
        this.statistics.available = results[2];
        
        // ËÆ°ÁÆóÊúàÊî∂ÂÖ•ÔºàËé∑ÂèñÂ∑≤ÁßüÊàøÈó¥ÁöÑÁßüÈáëÊÄªÂíåÔºâ
        await this.calculateMonthlyRevenue();
        
      } catch (error) {
        console.error('Âä†ËΩΩÁªüËÆ°Êï∞ÊçÆÂ§±Ë¥•:', error);
      }
    },
    
    // Ëé∑ÂèñÊàøÈó¥Êï∞Èáè
    async getRoomCount(status) {
      try {
        const result = await uniCloud.callFunction({
          name: 'room-management',
          data: {
            action: 'getRooms',
            data: {
              status,
              pageSize: 1000, // Ëé∑ÂèñÊâÄÊúâÊï∞ÊçÆÊù•ËÆ°ÁÆóÊÄªÊï∞
              pageNum: 1
            }
          }
        });
        
        return result.result.code === 0 ? result.result.data.total : 0;
      } catch (error) {
        console.error('Ëé∑ÂèñÊàøÈó¥Êï∞ÈáèÂ§±Ë¥•:', error);
        return 0;
      }
    },
    
    // ËÆ°ÁÆóÊúàÊî∂ÂÖ•
    async calculateMonthlyRevenue() {
      try {
        const result = await uniCloud.callFunction({
          name: 'room-management',
          data: {
            action: 'getRooms',
            data: {
              status: 'rented',
              pageSize: 1000,
              pageNum: 1
            }
          }
        });
        
        if (result.result.code === 0) {
          const rentedRooms = result.result.data.list;
          const totalRevenue = rentedRooms.reduce((sum, room) => {
            return sum + (room.rent_price || 0);
          }, 0);
          
          this.statistics.monthlyRevenue = totalRevenue;
        }
      } catch (error) {
        console.error('ËÆ°ÁÆóÊúàÊî∂ÂÖ•Â§±Ë¥•:', error);
      }
    },
    
    // Âä†ËΩΩÊúÄËøëÊ¥ªÂä®
    loadRecentActivities() {
      // ËøôÈáåÂèØ‰ª•‰ªéÊï∞ÊçÆÂ∫ìÂä†ËΩΩÊúÄËøëÁöÑÊìç‰ΩúËÆ∞ÂΩï
      // ÊöÇÊó∂‰ΩøÁî®Ê®°ÊãüÊï∞ÊçÆ
      this.recentActivities = [
        {
          id: 1,
          icon: 'üè†',
          title: 'Êñ∞Â¢û‰∫Ü101Âè∑ÊàøÈó¥',
          time: new Date(Date.now() - 2 * 60 * 60 * 1000) // 2Â∞èÊó∂Ââç
        }
      ];
    },
    
    // ÂØºËà™Âà∞ÊàøÈó¥ÂàóË°®
    navigateToRooms(status) {
      uni.switchTab({
        url: '/pages/room-list/room-list'
      });
    },
    
    // Ê∑ªÂä†ÊàøÈó¥
    addRoom() {
      uni.navigateTo({
        url: '/pages/room-edit/room-edit'
      });
    },
    
    // Êü•ÁúãÊàøÈó¥ÂàóË°®
    viewRooms() {
      uni.switchTab({
        url: '/pages/room-list/room-list'
      });
    },
    
    // Ê∞¥ÁîµÁÆ°ÁêÜ
    utilityRecords() {
      uni.navigateTo({
        url: '/pages/utility-record/utility-record'
      });
    },
    
    // ÁßüÊà∑ÁÆ°ÁêÜ
    tenantManagement() {
      uni.navigateTo({
        url: '/pages/tenant-info/tenant-info'
      });
    },
    
    // Á≥ªÁªüÊµãËØï
    systemTest() {
      uni.navigateTo({
        url: '/pages/test/test'
      });
    },
    
    // Ê†ºÂºèÂåñÊó∂Èó¥
    formatTime(time) {
      const now = new Date();
      const diff = now - time;
      const hours = Math.floor(diff / (1000 * 60 * 60));
      
      if (hours < 1) {
        return 'ÂàöÂàö';
      } else if (hours < 24) {
        return `${hours}Â∞èÊó∂Ââç`;
      } else {
        const days = Math.floor(hours / 24);
        return `${days}Â§©Ââç`;
      }
    }
  }
}
</script>

<style>
.container {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  min-height: 100vh;
  padding-bottom: 120rpx;
  /* #ifdef H5 */
  padding-bottom: 70px;
  /* #endif */
}

.welcome-section {
  padding: 80rpx 40rpx 60rpx;
  text-align: center;
  color: white;
}

.welcome-title {
  font-size: 48rpx;
  font-weight: bold;
  margin-bottom: 16rpx;
}

.welcome-subtitle {
  font-size: 28rpx;
  opacity: 0.8;
}

.stats-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 20rpx;
  padding: 0 40rpx 40rpx;
}

.stat-card {
  background: white;
  border-radius: 20rpx;
  padding: 40rpx 30rpx;
  text-align: center;
  box-shadow: 0 8rpx 25rpx rgba(0,0,0,0.1);
}

.stat-icon {
  font-size: 48rpx;
  margin-bottom: 16rpx;
}

.stat-number {
  font-size: 40rpx;
  font-weight: bold;
  color: #333;
  margin-bottom: 8rpx;
}

.stat-label {
  font-size: 24rpx;
  color: #666;
}

.quick-actions, .recent-activity {
  background: white;
  margin: 0 40rpx 30rpx;
  border-radius: 20rpx;
  padding: 40rpx 30rpx;
  box-shadow: 0 8rpx 25rpx rgba(0,0,0,0.1);
}

.section-title {
  font-size: 32rpx;
  font-weight: bold;
  color: #333;
  margin-bottom: 30rpx;
}

.action-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 30rpx;
}

.debug-item {
  grid-column: 1 / -1;
  background: #ff9500 !important;
}

.action-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 30rpx 20rpx;
  border-radius: 16rpx;
  background: #f8f9fa;
  transition: all 0.3s;
}

.action-item:active {
  transform: scale(0.95);
  background: #e9ecef;
}

.action-icon {
  font-size: 40rpx;
  margin-bottom: 12rpx;
}

.action-text {
  font-size: 26rpx;
  color: #333;
}

.activity-list {
  max-height: 400rpx;
}

.activity-item {
  display: flex;
  align-items: center;
  padding: 20rpx 0;
  border-bottom: 1rpx solid #f0f0f0;
}

.activity-item:last-child {
  border-bottom: none;
}

.activity-icon {
  font-size: 32rpx;
  margin-right: 20rpx;
}

.activity-content {
  flex: 1;
}

.activity-title {
  font-size: 28rpx;
  color: #333;
  margin-bottom: 8rpx;
}

.activity-time {
  font-size: 24rpx;
  color: #999;
}

.no-activity {
  text-align: center;
  padding: 60rpx 0;
  color: #999;
  font-size: 28rpx;
}
</style>
